<template>
  <div id="todoList">
    <div class="header">TODOLIST</div>
    <div class="todoBox">
      <el-dropdown @command="handleCommand" class="dropDown">
        <span class="el-dropdown-link">
          {{currentTab.value}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-for="(c, i) in chooseTab" :key="i" :command="c.id">{{c.value}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div class="list">
        <draggable :list="filterItems">
          <div class="unit" v-for="(item, index) in filterItems" :class="{'starBg' : item.star == 'true', 'complated' : item.complatedStatus}">
            <i class="glyphicon glyphicon-option-vertical"></i>
            <span class="checkBox">
              <input type="checkbox" :id="'c'+ (index+1)" v-model="item.complatedStatus"/>
              <label :for="'c'+ (index+1)"><span></span></label>
            </span>
            <div class="title">{{item.title}}</div>
            <span class="star" >
              <i class='icon-star'  @click="item.star = 'true'"></i>
              <i class="icon-starSelect" @click="item.star = 'false'" v-if="item.star == 'true'"></i>
            </span>
            <div class="icon">
              <span data-toggle="modal" @click="openeditmodal(index)"><i class="icon-edit"></i></span>
              <span :class="{'hidden' : !item.deadline}"><i class="icon-bell"></i> {{item.deadline}}</span>
              <span :class="{'hidden' : !item.file}"><i class="icon-message"></i></span>
              <span :class="{'hidden' : !item.comment}"><i class="icon-paperclip"></i></span>
            </div>
          </div>
        </draggable>
        
      </div>
      <div class="itemTotal">{{progressTotal}} Task left</div>
      <div class="addItem" data-toggle="modal" @click="openaddmodal">
        <i class="icon-add"></i>
      </div>
      <!-- add Modal -->
      <div class="modal fade" id="addModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
        <div class="modal-dialog modal-sm" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title" id="myModalLabel">Add Task</h4>
            </div>
            <div class="modal-body">
                <form>
                  <div class="form-group">
                    <label>Title</label>
                    <input type="text" class="form-control" v-model="addItem.title">
                  </div>
                  <div class="form-group">
                      <label><i class="icon-bell"></i>Deadline</label>
                      <input type="date" class="form-control" v-model="addItem.deadline">
                    </div>
                  <div class="form-group">
                    <label><i class="icon-paperclip"></i>File</label>
                    <div>
                      <span>test.jpg</span>
                      <a class="file"><i class="glyphicon glyphicon-plus"></i><input type="file" @change="getFile"></a>
                    </div>
                    <!-- <input type="file" @change="getFile"> -->
                  </div>
                  <div class="form-group">
                    <label><i class="icon-message"></i>Comment</label>
                    <textarea class="form-control" v-model="addItem.comment"></textarea>
                  </div>
                </form>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-default cancelBtn" data-dismiss="modal" @click="cancelTodo">Cancel</button>
              <button type="button" class="btn btn-primary saveBtn" @click="creatBtn">Add Task</button>
            </div>
          </div>
        </div>
      </div>
      <!-- edit Modal -->
      <div class="modal fade" id="editModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
          <div class="modal-dialog modal-sm" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h4 class="modal-title" id="myModalLabel">Edit Task</h4>
              </div>
              <div class="modal-body">
                  <form>
                    <div class="form-group">
                      <label>Title</label>
                      <input type="text" class="form-control" v-model="editItem.title">
                    </div>
                    <div class="form-group">
                        <label><i class="icon-bell"></i>Deadline</label>
                        <input type="date" class="form-control" v-model="editItem.deadline">
                      </div>
                    <div class="form-group">
                      <label><i class="icon-paperclip"></i>File</label>
                      <div>
                        <span>{{editItem.file}}</span>
                        <a class="file"><i class="glyphicon glyphicon-plus"></i><input type="file" @change="getFile"></a>
                      </div>
                    </div>
                    <div class="form-group">
                      <label><i class="icon-message"></i>Comment</label>
                      <textarea class="form-control" v-model="editItem.comment"></textarea>
                    </div>
                  </form>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-default cancelBtn" data-dismiss="modal" @click="cancelTodo">Cancel</button>
                <button type="button" class="btn btn-primary saveBtn" @click="editBtn">Update Task</button>
              </div>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
// import axios from 'axios';
import draggable from 'vuedraggable'
export default {
  name: 'todoList',
  components: {
    draggable
  },
  data () {
    return {
      chooseTab:[{
        id:1,
        value:'ALL'
      },{
        id:2,
        value:'Progress'
      },{
        id:3,
        value:'Completed'
      }],
      currentTab:{id:1,value:"ALL"},
      addIndex:"",
      addItem: [{
                    title:"",
                    deadline:"",
                    file:"",
                    comment:"",
                }],
      editItem: [],
      editIndex:"",
      filterItems:[],
      star:false,
      list:[{name:"John"}, 
				{name:"George"},
        {name:"Paul"},
				{name:"Ringo"} ],
      
      items:[{
                "title": "1avoids pleasure itself, because it is pleasure",
                "deadline": "2018-05-22",
                "file":"test.jpg",
                "comment": " expound the actual teachings of the great explorer of the truth, the master-builder of human happiness. No one rejects, dislikes, or avoids pleasure itself, because it is pleasure",
                "star":true,
                "complatedSelect":2,
                "complatedStatus":false,
                "id":1
              },
              {
                "title": "2avoids pleasure itself, because it is pleasure",
                "deadline": "2018-05-22",
                "file": "",
                "comment":"",
                "star":false,
                "complatedSelect":2,
                "complatedStatus":false,
                "id":2
              },
              {
                "title": "3avoids pleasure itself, because it is pleasure",
                "deadline": "",
                "file":"",
                "comment": "",
                "star":false,
                "complatedSelect":2,
                "complatedStatus":false,
                "id":3
              },
              {
                "title": "4avoids pleasure itself, because it is pleasure",
                "deadline": "",
                "file":"",
                "comment": "",
                "star":false,
                "complatedSelect":3,
                "complatedStatus":true,
                "id":4
              },
              {
                "title": "5avoids pleasure itself, because it is pleasure",
                "deadline": "",
                "file":"",
                "comment": "",
                "star":false,
                "complatedSelect":3,
                "complatedStatus":true,
                "id":5
              }
            ]
    }
  },
  mounted () {
    this.filterItems = this.items
    // axios
    //   .get('../../static/todo.json')
    //   .then(response => (this.info = response))
  },
  computed:{
    progressTotal(){
        var itemLen = [];
        this.items.forEach(function(item){
            if(item.complatedSelect != 3){
                itemLen.push(item);
            }
        })
        return itemLen.length;
    }
  },
  methods:{
    openaddmodal(i){
      this.addIndex = i
      $('#addModal').modal('show')
    },
    creatBtn(){
      var addItem = {
          'title':this.addItem.title,
          'deadline':this.addItem.deadline,
          'comment': this.addItem.comment
      }
      this.items.push(addItem);
      $('#addModal').modal('hide')
      this.addItem.title = ""
      this.addItem.deadline = ""
      this.addItem.comment = ""
    },
    cancelTodo(){
      this.addItem.title = ""
      this.addItem.deadline = ""
      this.addItem.comment = ""
    },
    openeditmodal(i){
      this.editIndex = i
      this.editItem = this.items[i];
      $('#editModal').modal('show')
    },
    editBtn(){
        $('#editModal').modal('hide')
    },
    // checkTodo(item){
    //   this.items = this.items.map(t => {
    //       if(t.id === item.id) {
    //         if(item.complatedStatus){
    //         item.complatedSelect = 3
    //       }else{
    //         item.complatedSelect = 2
    //       }
    //       return item
    //     }
    //   })
    // },
    handleCommand(command){
      this.chooseTab.every(ele=>{
        if(command == ele.id){
          this.currentTab = ele
          return false
        }
        return true
      })
      if(command != 1){
         this.filterItems = this.items.filter(t => {
            return t.complatedSelect === command
        })
      }else{
        this.filterItems = this.items
      }
     
    },
    getFile(item){
      this.newFile= 'updateTest.jpg'
		  item.file = 'test.jpg';
      this.editItem.file = this.newFile;
    }
  }
}
</script>
<style>

.input-group-addon{
  background: #4A90E2;
  color: #fff;
  border:1px solid #4A90E2;
}

</style>








